@-webkit-keyframes high {


  0% {background: -webkit-gradient(linear, left top, right top, color-stop(50%, yellow), color-stop(50%, white));background: -webkit-linear-gradient(left, yellow  50%, white 50%);background: linear-gradient(to right, yellow  50%, white 50%);
    background-size: 200% 100%;
    background-position:right bottom;
}

100%{  
  background: -webkit-gradient(linear, left top, right top, color-stop(50%, yellow), color-stop(50%, white));  
  background: -webkit-linear-gradient(left, yellow 50%, white 50%);  
  background: linear-gradient(to right, yellow 50%, white 50%);
    background-size: 200% 100%;

  background-position:left bottom;
text-decoration: underline;


}


}


