@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(500%);
            transform: translateX(500%);
    -webkit-transform: scale(9);
            transform: scale(9);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
  }
}

@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(500%);
            transform: translateX(500%);
    -webkit-transform: scale(9);
            transform: scale(9);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
  }
}




@-webkit-keyframes fadeSub {
  0% {opacity: 0;
    color: red;}


  80% {opacity: 0;
    color: black;}


  100%{opacity: 1;
    color: black;};


}




@keyframes fadeSub {
  0% {opacity: 0;
    color: red;}


  80% {opacity: 0;
    color: black;}


  100%{opacity: 1;
    color: black;};


}


